.contacts {
  &__map {
    height: 546px;
    background: gray;
    filter: grayscale(100%);
    transform: translate3d(0,0,0);
    @include m {
      height: 245px; } }
  &__wrap {
    padding: 90px 0;
    @include m {
      padding: 33px 0; } }
  &__center {
    display: flex;
    @include t {
      display: block; } }
  &__col {
    &:nth-child(1) {
      flex: 0 0 60%;
      padding-right: 40px;
      @include t {
        margin-bottom: 80px;
        padding: 0; }
      @include m {
        margin-bottom: 20px; } } }
  &__title {
    margin-bottom: 60px;
    font-size: 50px;
    line-height: 1.2;
    @include m {
      margin: 0;
      font-size: 26px;
      line-height: 1.27; } }
  &__btn {
    @include m {
      display: none; } }
  &__item {
    padding-top: 8px;
    @include m {
      padding: 0 0 16px; }
    &:not(:last-child) {
      margin-bottom: 25px;
      @include m {
        margin-bottom: 18px;
        border-bottom: 1px solid #EAEAEA; } } }
  &__type {
    margin-bottom: 8px;
    opacity: .6;
    font-size: 18px;
    text-transform: uppercase;
    letter-spacing: 4px;
    @include m {
      margin-bottom: 6px; } }
  &__text {
    font-size: 30px;
    color: $cl;
    transition: opacity .2s;
    &:hover {
      opacity: .7; }
    @include m {
