.subscribe {
  display: flex;
  @include m {
    display: block; }
  &__input,
  &__btn {
    height: 60px;
    font-size: 20px; }
  &__input {
    width: 297px;
    padding: 0 20px 4px;
    border-radius: 3px 0 0 3px;
    @include m {
      margin-bottom: 10px; } }
  &__btn {
    min-width: 150px;
    padding: 0 40px 4px;
    background: $blue;
