      background: lighten($blue,5); } }
  &__input,
  &__btn {
    @include m {
      width: 100%;
      height: 50px;
      border-radius: 3px; } } }

.social {
  display: flex;
  align-items: center; }

.footer {
  background: #101010;
  color: #fff;
  @include m {
    text-align: center; }
  &__wrap {
    padding: 40px 0; }
  &__wrap:nth-child(1) &__center {
    @include t {
      display: block; } }
  &__wrap:nth-child(2) {
    border-top: 1px solid rgba(#fff,.1);
    @include m {
      padding: 30px 0; } }
  &__center {
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include m {
      display: block; } }
  &__text {
    padding-right: 30px;
    font-size: 30px;
    @include t {
      margin-bottom: 30px; }
    @include m {
      padding: 0;
      font-size: 26px; } }
  &__copyright {
    opacity: .4;
    font-size: 22px;
    @include m {
      margin-bottom: 30px;
      font-size: 20px; } }
  &__right {
