    align-items: center;
    @include m {
      display: block;
      border: 1px solid rgba(#fff,.1); } }
  &__nav &__link {
    @include m {
      display: block;
      margin: 0;
      padding: 15px;
      opacity: 1;
      font-size: 16px;
      color: #FFF;
      letter-spacing: 2.6px;
      text-transform: uppercase;
      &:not(:last-child) {
        border-bottom: 1px solid rgba(#fff,.1); } } }
  &__social {
    margin-left: 35px;
    @include m {
      margin: 0;
      padding-top: 30px;
      justify-content: center; } }
  .icon {
    display: block; }
  .icon-facebook {
    font-size: 24px; }
  .icon-twitter {
    font-size: 20px; } }

.promo {
  display: flex;
  position: relative;
  height: 520px;
  margin-top: -120px;
  padding: 50px 0;
  align-items: center;
  text-align: center;
  @include m {
    height: 362px;
    margin-top: -71px; }
  &__title {
    padding-top: 42px;
    font-size: 62px;
    line-height: 1.24;
    letter-spacing: 16.6px;
    text-transform: uppercase;
    color: #FFF;
    @include m {
      margin-bottom: 32px;
      font-size: 28px;
      letter-spacing: 7px; } } }

.info {
  &__preview {
    margin-bottom: 80px;
    @include m {
      margin: 0 -20px 40px; } }
  &__pic {
    display: block;
    width: 100%; }
  &__wrap {
    display: flex;
    margin-left: -27px;
    @include t {
      display: block;
